@import url("https://fonts.googleapis.com/css2?family=Alkatra&family=Manrope:wght@300&family=Montserrat:wght@200;500&family=Raleway:wght@400;700&display=swap");
footer {
  padding: 50px 0px 20px 0px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  background-color: black;
  position: relative;
  bottom: 0px;
}
@media all and (min-width: 769px) and (max-width: 1024px) {
  footer {
    padding: 50px 0px 50px 0px;
  }
}
@media all and (min-width: 641px) and (max-width: 768px) {
  footer {
    padding: 50px 0px 50px 0px;
  }
}
@media all and (max-width: 640px) {
  footer {
    gap: 15px;
    padding: 50px 0px 50px 0px;
  }
}

@media all and (min-width: 641px) and (max-width: 768px) {
  img img {
    width: 180px;
    height: auto;
    object-fit: contain;
  }
}
@media all and (max-width: 640px) {
  img img {
    width: 145px;
    height: auto;
    object-fit: contain;
  }
}

.droits {
  color: white;
  font-size: 24px;
}
@media all and (min-width: 769px) and (max-width: 1024px) {
  .droits {
    font-size: 20px;
    width: 418px;
    text-align: center;
  }
}
@media all and (min-width: 641px) and (max-width: 768px) {
  .droits {
    font-size: 16px;
    width: 323px;
    text-align: center;
  }
}
@media all and (max-width: 640px) {
  .droits {
    font-size: 12px;
    width: 134px;
    text-align: center;
  }
}

