.item {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}
.item__block {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #ff6060;
  height: 52px;
  width: 100%;
}
@media all and (min-width: 769px) and (max-width: 1024px) {
  .item__block {
    height: 41px;
  }
}
@media all and (min-width: 641px) and (max-width: 768px) {
  .item__block {
    height: 36px;
  }
}
@media all and (max-width: 640px) {
  .item__block {
    height: 36px;
  }
}
.item__block__button {
  background-color: #ff6060;
  border: none;
  color: white;
  padding-right: 15px;
  padding-bottom: 5px;
  cursor: pointer;
  border-radius: 10px;
}
@media all and (min-width: 769px) and (max-width: 1024px) {
  .item__block__button {
    height: 41px;
  }
}
@media all and (min-width: 641px) and (max-width: 768px) {
  .item__block__button {
    height: 36px;
  }
}
@media all and (max-width: 640px) {
  .item__block__button {
    height: 36px;
  }
}
.item__txt {
  max-height: 0;
  overflow: hidden;
  transition: max-height 2s ease-in-out;
  background-color: #f6f6f6;
  color: #ff6060;
  border-radius: 0px 0px 10px 10px;
}
.item__txt__p {
  padding: 20px;
  margin: 0px;
}
@media all and (min-width: 769px) and (max-width: 1024px) {
  .item__txt__p {
    font-size: 16px;
  }
}
@media all and (min-width: 641px) and (max-width: 768px) {
  .item__txt__p {
    font-size: 16px;
  }
}
@media all and (max-width: 640px) {
  .item__txt__p {
    font-size: 15px;
  }
}
.item__txt__description {
  font-size: 18px;
  padding: 18px;
}
.item__txt__open {
  max-height: 1000px;
}

